
@mixin tr {
    transition: all .3s ease-in-out;
}

@mixin hover {
    @include tr;
    &:hover,
    &:focus,
    &:active {
        @content;
        @include tr;
    }
}

@mixin screen($width) {
    @media screen and (max-width: $width) {
        & {
            @content;
        }
    }
}
